* {
    box-sizing: border-box;
}

html,
body {
    padding: 0;
    margin: 0;
	background: #fffffe;
    font-family: $colfax;
    font-weight: 400;
    font-size: 16px;
    color: $dark;
	overflow-x: hidden;
    @include smoothing();
}

img {
	max-width: 100%;
}

// -----------------------------------------------------------------------------
//! Sections
// -----------------------------------------------------------------------------

section {
	width: 100%;
    max-width: $content-width;
    margin: 0 auto;
	padding: 2.5em 0;
	background: white;

    &.fill {
        position: relative;

        &:after {
            content: "";
            position: absolute;
            left: 0;
            bottom: -31%;
            width: 100%;
            height: 75%;
            background-image: linear-gradient(-180deg,transparent,#1a4878);
            opacity: .08;
            pointer-events: none;
        }
    }

    &.callout-padding {
    	padding: 1.5em 0;

    	@include min( $sm ) {
    		padding: 2.438em 0;
    	}

    	@include min( $lg ) {
    		padding: 3.688em 0;
    	}

    	@include min ( $lg ) {
    		padding: 6.85rem 0;
    	}
    }

    &.testimonial-padding {
        padding: 0 0 4rem;

        @include min( $sm ) {
    		padding: 0 0 4.438em ;
    	}

    	@include min( $lg ) {
    		padding: 0 0 5.688em ;
    	}
    }

	@include min( $sm ) {
		padding: 3.438em 0;
	}

	@include min( $lg ) {
		padding: 4.688em 0;
	}

    @include min( $content-width ) {
        max-width: 100%;
    }

}

.container {
	position: relative;
	max-width: $content-width;
	margin: auto;
	padding: 0 15px;

	@include min( $xs ) {
		padding: 0 20px;
	}

	@include min( $sm ) {
		padding: 0 30px;
	}

	@include min( $lg ) {
		padding: 0 40px;
	}
}

.container .full-width {
	margin-left: calc(-50vw + 50%);
	margin-right: calc(-50vw + 50%);
}
