// -----------------------------------------------------------------------------
//! Fonts
// -----------------------------------------------------------------------------

@font-face {
  font-family: 'Colfax';
  src: url('../../src/fonts/colfax.eot'); /* IE9 Compat Modes */
  src: url('../../src/fonts/colfax.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../../src/fonts/colfax.woff2') format('woff2'), /* Super Modern Browsers */
       url('../../src/fonts/colfax.woff') format('woff'), /* Pretty Modern Browsers */
       url('../../src/fonts/colfax.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('../../src/fonts/colfax.svg#svgFontName') format('svg'); /* Legacy iOS */
}

$colfax: 'Colfax', sans-serif;
$roboto-medium: 'Roboto Mono', monospace;

// -----------------------------------------------------------------------------
//! Colors
// -----------------------------------------------------------------------------

$dark: #2E353E;
$gray: #515F73;
$blue: #2C7CB5;
$white: #FFFFFF;
$yellow: #FFFA6E;

// -----------------------------------------------------------------------------
//! Breakpoints & Sizes
// -----------------------------------------------------------------------------

$xs: 420px;
$sm: 768px;
$md: 992px;
$lg: 1100px;
$content-width: 1440px;
