// -----------------------------------------------------------------------------
//! Paragraphs
// -----------------------------------------------------------------------------

p {
	margin: 10px 0;
	font-size: 1rem;
	line-height: 28px;
	color: $gray;

	&.lead {
		font-size: 1.1875rem;
		line-height: 36px;

		@include min( $sm ) {
			font-size: 1.5rem;
		}
	}

	&.small {
		font-size: 0.875rem;
	}

	&.xs {
		font-size: 0.8125rem;
	}

	@include min( $sm ) {
		font-size: 1.0625rem;
	}
}

p:first-child {
	margin-top: 0;
}

p:last-child {
	margin-bottom: 0;
}

// -----------------------------------------------------------------------------
//! Headings
// -----------------------------------------------------------------------------

h1 {
	margin: 2.076rem 0;
	font-size: 2.375rem;
	font-weight: 400;
	color: $dark;
}

h2 {
	font-size: 1.5rem;
	font-weight: 400;
	color: $blue;

	@include min( $sm ) {
		font-size: 2.0625rem;
	}
}

h3 {
	margin-bottom: 1.6875rem;
	color: $gray;
	font-weight: 400;

	&.small {
		font-size: 0.875rem;
	}

	&.opaque {
		opacity: 0.25;
	}

	@include max( $sm ) {
		font-size: 1rem;
	}
}

// -----------------------------------------------------------------------------
//! Lists
// -----------------------------------------------------------------------------


// -----------------------------------------------------------------------------
//! Links
// -----------------------------------------------------------------------------

a,
a:visited {
	color: $dark;
	font-size: 1rem;
	text-decoration: none;
	transition: .2s;
}
